import React, { Component } from 'react'



export class Navbar extends Component {

  constructor(props) {
    super(props)
  
    this.toggleClass= this.toggleClass.bind(this);
    this.state = {
      activeIndex: 0
    }
  }
  
  toggleClass(index, e) {
  
    this.setState({ activeIndex: index });
  };

  render() {
    return (
      <navbar  className="navbar navbar-expand-lg navbar-dark fixed-top bg-dark" id="nav">
        <div className="container-fluid">
          <button className="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarsExampleDefault" aria-controls="navbarsExampleDefault" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarsExampleDefault">
            <ul className="navbar-nav mr-auto">
              <li className={this.state.activeIndex==0 ? 'active': null}  onClick={this.toggleClass.bind(this, 0)}><a className="nav-link" href="#intro">Home</a></li>
              <li className={this.state.activeIndex==1 ? 'active': null}  onClick={this.toggleClass.bind(this, 1)}><a className="nav-link" href="#about">About</a></li>
              <li className={this.state.activeIndex==2 ? 'active': null}  onClick={this.toggleClass.bind(this, 2)}><a className="nav-link" href="#products">Products</a></li>
              <li className={this.state.activeIndex==3 ? 'active': null}  onClick={this.toggleClass.bind(this, 3)}><a className="nav-link" href="#menu">Menu</a></li>
              <li className={this.state.activeIndex==4 ? 'active': null}  onClick={this.toggleClass.bind(this, 4)}><a className="nav-link" href="#contact">Contact</a></li>
              <li className={this.state.activeIndex==5 ? 'active': null}  onClick={this.toggleClass.bind(this, 5)}><a className="nav-link" href="#location">Location</a></li>
              {/* <li className={this.state.activeIndex==4 ? 'active': null}  onClick={this.toggleClass.bind(this, 4)}><a className="nav-link" href="#contact">AdminPage</a></li> */}
            </ul>
          </div>
        </div>
      </navbar>
    )
  }
}
